.slide-main {
  position: relative;
  overflow: hidden;
  .swiper-slide {
    height: 500px;
    background-size: cover;
    background-position: center bottom;
  }

  .swiper-button-next {
    top: inherit;
    bottom: 0;
  }

  .slide-next,
  .slide-prev {
    position: absolute;
    z-index: 1;
    background-color: $blue;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
        font-size: 22px;
        color: #fafafa;
    }
  }

  .slide-next {
      right: 0;
      bottom: 0;
  }

  .slide-prev  {
      right: 50px;
      bottom: 0;
  }
}

.slide-home {
  height: 100%;
}